.container {
	max-width: 450px;
	margin: 80px auto;
	border: 2px solid rgb(255, 99, 71);
	padding: 30px;
	border-radius: 10px;

}
p {
	display: flex;
	justify-content: center;
	text-align: center;
	color: white;
	font-size: 8pt;
}
.nav {
	position: fixed;
	bottom: 0;
	width: 100%;
	height: 50px;
	display: flex;
	background-color: tomato;
	justify-content: space-between;
	align-items: center;
	text-align: center;
}

.git {
	display: flex;
	width: 30px;
	filter: invert(100%);
}
.a-git {
	margin-right: 200px;
}
.a-logo {
	margin-left: 200px;
}
.logo  {
	display: flex;
}

.alert {
	display: flex;
	position: fixed;
	height: 300px;
	width: 300px;
	background-color: rgb(65,65,65);
}
