.task-container {
	background-color: #444;
	margin: 8px 0;
	padding: 15px 20px;
	display: flex;
	border-radius: 5px;
	justify-content: space-between;
	color: #eee;
	align-items: center;
}

.task-title {
	cursor: pointer;
}

.buttons-container {
	width: 20%;
	display: flex;
	justify-content: flex-end;
}

.remove-task-button {
	background-color: #444;
	border: none;
	width: 100px;
	padding: 5px;
	color: white;
	margin: 5px 0px;
	margin-left: 5px;
	background-color: tomato;
	border-radius: 3px;
}
.remove-task-button:hover {
	background-color: rgba(255, 99, 71,0.800);
	transition: 0.2s;
	border-radius: 5px;
	cursor: pointer;
}
.remove-task-button:active {
	background-color: rgba(255, 99, 71, 0.300);
}
