.button {
	background-color: tomato;
	height: 40px;
	padding: 0 10px;
	border-radius: 5px;
	width: 100%;
	font-size: 16px;
	color: white;
	font-weight: bold;
	cursor: pointer;
	border: none;
	transition: 0.2s;
}

.button:hover {
	background-color: rgba(255, 99, 71, 0.700);
}
.button:active {
	background-color: rgba(255, 99, 71, 0.300);
}
